.HomeBackground {
    background-color: rgb(5, 5, 5);
    background-image: url(/AdamWellsProductions/static/media/keyboard.5371ea85.jpg);
    /*background-image: none;*/  /*Used to cancel out background image to show initial background*/
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover; /* Resize the background image to cover the entire container */
    width: 100%;
    height: 950px;
}
.content {
    display: flex;
    align-items: center; /* Vertical center alignment */
    flex-direction: column;
    padding-right: 850px;
}
.content p {
    font-weight: bold;
    padding: 5px;
    font-size: 35px;
    text-align: center;
    color: white;
    text-shadow: none;
}
.fifthFont {
    font-family: "Roboto", Arial, sans-serif;
    color: white;
    font-weight: bold;
    font-size: 40px;
    padding-right: 20px;
    padding-top: 0px;
}
.centralFont {
    font-family: "Roboto", Arial, sans-serif;
    padding-top: 250px;
    padding-bottom: 120px;
}
.centralFont p {
    font-weight: bold;
    padding: 5px;
    font-size: 35px;
    text-align: center;
    color: white;
    border-style: solid;
    border-width: 0px;
    border-bottom-width: 1px;
    border-top-width: 1px;
