    background-color: rgba(0, 0, 0, 0.5);
    margin-bottom: 0;
}
.centralFont text {
    color: white;
    font-size: 40px;
    float: left;
    text-shadow: 5px 4px 20px 5px black;
}
.nameContainer {
    padding-top: 50px;
