    height: 528px;
    background-color: white;
    border-radius: 40%;
}
.wordBlock {
    position:relative; left:800px;
}


.typingeffect {
    overflow: hidden;
    /*width: 55%;*/
}
.typingeffect text {
    border-right-style: solid;
    border-color: transparent;
    overflow: hidden;
    white-space: nowrap;
    margin: 0 auto;
    padding-right: 20px;
    -webkit-animation: typewriter 4s steps(40, end);
            animation: typewriter 4s steps(40, end);
}

@-webkit-keyframes typewriter{
    from{ width: 0%; opacity: 0; border-right: .05em white;}
    to{ width: 100%; opacity: 100%; border-right: 0em;}
}

@keyframes typewriter{
    from{ width: 0%; opacity: 0; border-right: .05em white;}
    to{ width: 100%; opacity: 100%; border-right: 0em;}
}
@-webkit-keyframes fadeIn{
    from{ opacity: 0; }
    to{ opacity: 100%; }
}
@keyframes fadeIn{
    from{ opacity: 0; }
    to{ opacity: 100%; }
}
@media screen and (max-width: 600px) {
