        padding-top: 80px;
    }
    .centralFont text {
        font-size: 25px;
    }
    .centralFont p {
        font-size: 25px;
    }
    .content p {
        font-size: 25px;
    }
    .fifthFont {
        font-size: 20px;
        padding-bottom: 50px;
    }
}
@media screen and (max-width: 1200px) and (min-width: 601px) {
