.contact {
    background-color: rgb(8, 163, 253);
    display: flex;
    flex-direction: column;
    min-height: 100vh;  
}
.contact h2 {
    font-size: 60px;
    text-align: center;
    text-shadow: 0 1px 0 #ccc;
    line-height: 10px;
}
.contact h3 {
    font-size: 50px;
    text-align: center;
    text-shadow: 0 1px 0 #ccc;
    line-height: 10px;
}
.contact h4 a {
    display: block;
    margin: auto;
    padding: 1rem 1.25rem;
    font-family: sans-serif;
    font-size: 1.5rem;
    text-decoration: none;
    color: black;
    position: relative;
    -webkit-transition: all .3s cubic-bezier(.2, 0, 0, 1);
    transition: all .3s cubic-bezier(.2, 0, 0, 1);
    z-index: 1;
}
.contact h4 a:after {
    content: '';
    display: block;
    height: 2px;
    position: absolute;
    bottom: 0;
    right: 1.25rem;
    left: 1.25rem;
    background-color: black;
    -webkit-transform-origin: bottom center;
            transform-origin: bottom center;
    z-index: -1;
  }
  
.contact h4 a:hover {
    color: white;
    background-color: black;
  }
.contact h4 {
    font-size: 40px;
    border-radius: 10px;
}
.contact_center {
    text-align: center;
    margin: 0 auto;
    width: 50%;
}

@media screen and (max-width: 600px) {
    .contact h2 {
        display: none;
    }
    .contact h3 {
        font-size: 30px;
        text-align: center;
    }
    .contact h4 a {
        font-size: 1.2rem;
    }
    .contact_center {
        text-align: center;
        margin: 0 auto;
        width: 100%;
    }
}
@media screen and (max-width: 1200px) and (min-width: 601px) {
    .contact {
        min-height: 60vh;  
